import {useState,useEffect} from 'react';
import './App.css';
import MainContainer from './components/MainContainer'; 
import Header from './components/Header';
import ToDoBox from './components/ToDoBox';
import AddContainer from './components/AddContainer';
import ToDo from './components/ToDo';


function App() {

  
  const [todoList,setTodoList]=useState([{text:'Exemplo',checked:false}]);
  
   useEffect(()=>{
     if(localStorage.length<1){localStorage.setItem('todos',JSON.stringify([{text:'Exemplo',checked:false}]))}
     
     setTodoList(JSON.parse(localStorage.getItem('todos')!))
    
  },[])
  
   

  function addToDo(newTodo:string){
    const listClone = [...todoList];
    
    if(newTodo!==''){
    listClone.push({text:newTodo,checked:false});
      }

  
    setTodoList(listClone);
    localStorage.setItem('todos',JSON.stringify(listClone))
    
   console.log(listClone)
  }

  function delToDo(index:number) {
    
    let listClone = [...todoList];

    listClone.splice(index,1);

    setTodoList(listClone);
    
    localStorage.setItem('todos',JSON.stringify(listClone))
    console.log(listClone)

  }

  function  doneTodo(index:number) {
    let listClone=[...todoList]
    listClone[index].checked=!listClone[index].checked
    setTodoList(listClone)
    localStorage.setItem('todos',JSON.stringify(listClone))
  }

  return (
    <MainContainer>
      <Header/>
      <ToDoBox>
       {todoList.map((element,index)=>{return(
         <ToDo del={delToDo} done={doneTodo} chave={index} checado={element.checked}>{element.text}</ToDo>
              
       )})}
       
      </ToDoBox>  
      <AddContainer add={addToDo}/>
     
    </MainContainer>
    
  );
}

export default App;
