import {useState} from 'react';
import '../../App.css';

export default function ToDo(props:{done(index:number):void,del(index:number):void,chave:number,checado:boolean,children:string}) {
    
    

    return(<div className="ToDo">
    <p style={{textDecoration: props.checado===true ? 'line-through':'none'}}>{props.children}</p>
    
    <div className="options">
        
        <input onClick={()=>{props.done(props.chave);}} checked={props.checado} type="checkbox"/>
        <button onClick={()=>props.del(props.chave)}>Apagar</button>
    </div>
</div>);
   
}